

/* eslint-disable no-unused-vars */


import React, { Component } from "react"
// import MetaTags from 'react-meta-tags';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  Button,
  // Form,
  Input,
  // InputGroupAddon,
  // InputGroupText,
  // InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
} from "reactstrap"

import {
  CButton,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'

import { French } from "flatpickr/dist/l10n/fr.js";

// import Flatpickr from "react-flatpickr"
import Flatpickr from "flatpickr";
import { storage } from "../../../firebase/firebase";
import DatePicker from "react-datepicker";
import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
import $ from 'jquery';
import conf from '../../../conf.js';
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
import ModalMessage from "../../pages/messages/ModalMessage"
const animatedComponents = makeAnimated();

// const cities = require('all-the-cities');

registerLocale('es', es)


const optionGroup = [
    {
      
      options: [
        { label: "PDF", value: "PDF" },
        { label: "MP4", value: "MP4" },
        { label: "JPEG", value: "JPEG" },
        { label: "MP3", value: "MP3" },
        { label: "EXCEL", value: "EXCEL" },
        
      ]
    },
    
  ]
  const optionLangue = [
    {
      options: [
        { label: "Francais", value: "Francais" },
        { label: "Anglais", value: "Anglais" },
        { label: "Wolof", value: "Wolof" }
      ]
    },
  ]
  
class ModifierSalle extends Component {
    constructor(props) {
        super(props)
        this.state = {
          terrainData:[],
          terrain:[],
          ok:"",
          ville:"",
          user:{},
          show:false,
          success:true,
          boutton:"OK",
          message:"Salle Modifié avec succès !!",
          titre:"Message modification de salle",
          files_preview:"",
          files_name:{file:""},
          multimedia:{file:""},
          multiUpdate:{},
          prix:[],
          user:JSON.parse(localStorage.getItem('token_auth')).user
        }
        this.handleChangeVille.bind(this)
        this.handleSavePrice.bind(this)
    }

    componentDidMount() {



      
      let  data=  JSON.parse(localStorage.getItem('token_auth'))
      var id=localStorage.getItem("idTerrain")
      const requestOptions = {
        method: 'GET',
        headers: { 
          'Authorization':'Bearer '+data.token,
    
        },
      };
  
      fetch(conf.url+"terrain/terrains/"+id,requestOptions)
      .then((response) => response.json())
      .then((data) => {
        console.log("data",data)
        this.setState({terrain:data,prix:data.prix})
         $("#nom").val(data.nom)
         $("#adresse").val(data.adresse)
         this.setState({ville:data.ville})
         $("#latitude").val(data.latitude)
         $("#longitude").val(data.longitude)
         $("#disponibilite").val(data.disponibilite)
         $("#description").val(data.description)
         $("#prixheure").val(data.prixHeure)
         $("#prixdemiheure").val(data.prixDemiHeure)

         data.prix.map((val)=>{
          $("#"+val.jour).val(val.valeur)
         })


         if(data.multimedias.length){
          storage.ref("images")
          .child(data.multimedias[0].file).getDownloadURL()
          .then((url) => {
            
              this.setState({files_preview:url})
              this.setState({multiUpdate:data.multimedias[0]})
          
          })
         }

        });

        fetch(conf.url+"user/users/"+data.id,requestOptions)
        .then((response) => response.json())
        .then((data) => {
           console.log("okkk====>",data)
          
            this.setState({user:data})
        
         
         }
         
        );


        $("input[data-preview]").change(() =>{
          var data=[]
          console.log("fiel =====>",Array.from(document.getElementById("autreFile").files))
          var date=new Date()
          
          var my_files = Array.from(document.getElementById("autreFile").files)
            var fileNom=date.getFullYear()+""+date.getMonth()+""+date.getDay()+""+date.getSeconds()+""+date.getMilliseconds()+""+my_files[0].name
            this.setState({files_name:{file:my_files[0],fileName:fileNom},multimedia:{file:fileNom}})
            
            var fileReader = new FileReader();
            fileReader.readAsDataURL(my_files[0]);
            fileReader.onload = (fileEvent)=> {
              this.setState({files_preview:fileEvent.target.result})
        
            };
    
        
        });
    }

    handleChangeVille = (e) => {
     
      console.log("adate =====>",e)
      try{
        this.setState({ville:e.label})
       
      }catch(ee){
  
      }
    
    }

    handleUpdateTerrain=()=>{
      const jours=["LUNDI","MARDI","MERCREDI","JEUDI","VENDREDI","SAMEDI","DIMANCHE"]
      var media=null
      var id=localStorage.getItem("idTerrain")
      let  token=  JSON.parse(localStorage.getItem('token_auth'))
      var data= {
        "id":id,
        "nom": $("#nom").val(),
        "adresse": $("#adresse").val(),
        "ville":this.state.ville,
        "latitude":$("#latitude").val(),
        "longitude": $("#longitude").val(),
        "disponibilite": $("#disponibilite").val(),
        "description": $("#description").val(),
        "prixHeure":$("#prixheure").val(),
        "prixDemiHeure":$("#prixdemiheure").val(),
        "disponibilite":"7h-18h",
        "user":this.state.user,
        "reservations":null,
        "multimedias":[this.state.multiUpdate],
        "idUser":this.state.user.id,
        "dateInscription":this.state.terrain.dateInscription
    
      }

      if(this.state.files_name.file!=""){
        media=this.state.multimedia
        storage.ref("images/"+this.state.files_name.fileName).put(this.state.files_name.file).then(()=>{})
        data= {
          "id":id,
          "nom": $("#nom").val(),
          "adresse": $("#adresse").val(),
          "ville":this.state.ville,
          "latitude":$("#latitude").val(),
          "longitude": $("#longitude").val(),
          "disponibilite": $("#disponibilite").val(),
          "description": $("#description").val(),
          "prixHeure":$("#prixheure").val(),
          "prixDemiHeure":$("#prixdemiheure").val(),
          "disponibilite":"7h-18h",
          "user":this.state.user,
          "reservations":null,
          "idUser":this.state.user.id,
          "multimedias":[media]
      
        }
  
      }
  
      console.log("data a transmettre",data)
  
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json','Accept': 'application/json',
        'Authorization':'Bearer '+token.token,
      },
        body: JSON.stringify(data)
    };
  
    fetch(conf.url+"terrain/update",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("data ok",data)
      try{
        if(data.id){
         
            this.state.prix.map((val,idx)=>{
               this.handleSavePrice({typeDePrix:"heure",valeur:$("#"+val.jour).val(),jour:val.jour,id:val.id},data.id)
               if(idx==6){
                 this.setState({show:true,success:true});
               }
            })
            if(this.state.prix.length===0){
              jours.map((val,idx)=>{
                this.handleSavePrice({typeDePrix:"heure",valeur:$("#"+val).val(),jour:val},data.id)
                if(idx==6){
                  this.setState({show:true,success:true});
                }
             })
            }
           
        }else{
         this.setState({show:true,success:false});
        }
     }catch(err){
       this.setState({show:true,success:false});
     }
    
     }
     
    );
      
    }

    handleSavePrice=(data,id)=>{
      let  token=  JSON.parse(localStorage.getItem('token_auth'))
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json','Accept': 'application/json',
        'Authorization':'Bearer '+token.token,
      },
        body: JSON.stringify(data)
    };

    if(this.state.terrain.prix.length){
      fetch(conf.url+"prix/update/"+id,requestOptions)
      .then((response) => response.json())
      .then((res) => {
      })
    }else{
      fetch(conf.url+"user/add/prix/terrain/"+id,requestOptions)
      .then((response) => response.json())
      .then((res) => {
      })
    }
   
   
   
      
    }

    handleValidate=()=>{
      if(this.state.success){
        this.setState({ok: <Redirect to='/liste-salle'/>});
      }else{
        this.setState({show:false})
      }
      
    }

 render() {
    const { pick } = this.state
    const { selectedGroup } = this.state
    const { selectedLangue } = this.state
    const animatedComponents = makeAnimated();

    const cities = [
      {
        value: 1, 
        label:"Dakar"
      },
      {
        value:2, 
        label:"Casamance"
      },
      {
        value:3, 
        label:"Pikine"
      },
      {
        value:4, 
        label:"Thiés"
      },
      {
        value:5, 
        label:"Kaolack"
      }
    
    ];

  return (
        <>
          <React.Fragment>
            <div className="page-content">
           
           {this.state.ok}

           <ModalMessage
              show={this.state.show}
              success={this.state.success}
              boutton={this.state.boutton}
              handleValidate={this.handleValidate}
              message={this.state.message}
              titre={this.state.titre}
              onCloseClick={() => this.setState({ show: false })}
              />

            <Container fluid={true} className="justify-content-center">

              <CRow className="justify-content-center">
              <CCol md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Modifier la salle</h1>
                  {/* </CCardHeader> */}

                  <CardBody>
                    <Row> 
                      <Col md="4">
                        <label>Nom</label>
                        <Input
                            className="form-control"
                            type="text"
                            id="nom"
                            placeholder="Nom"
                        />
                      </Col>
                      <Col md="4">
                        <label>Adresse</label>
                        <Input
                            className="form-control"
                            type="text"
                            id="adresse"
                            placeholder="Adresse"
                        />
                      </Col>
                      <Col md="4">
                        <label>Ville</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeVille}
                          options={cities}
                          placeholder={this.state.ville}
                        />
                      </Col>
                    </Row>
                  
                    <Row className="mt-4">
                      <Col md="12">
                        <label>Description</label>
                        <textarea  className="form-control" id="description" placeholder="Description" />
                      </Col>
                    </Row>

                    <Row className="mt-4">
                      <Col md="6">
                        <label>Latitude</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="latitude"
                          placeholder="Latitude"
                        />
                      </Col>
                      <Col md="6">
                      <label>Longitude</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="longitude"
                          placeholder="Longitude"
                        />
                      </Col>
                    </Row>

                    <hr/>
                    <Row className="mt-4">
                       
                        <Col md="2">  
                          <label>Prix Lundi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de lundi"
                              id="LUNDI"
                          />
                         </Col>
                       
                        <Col md="2">  
                        <label>Prix Mardi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de mardi"
                              id="MARDI"
                          />
                         </Col>

                         <Col md="2">
                          <label>Prix Mercredi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de mercredi"
                              id="MERCREDI"
                          />
                        </Col>
                        <Col md="2">  
                        <label>Prix Jeudi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de jeudi"
                              id="JEUDI"
                          />
                        </Col>
                        <Col md="2">
                          <label>Prix Vendredi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de vendredi"
                              id="VENDREDI"
                          />
                        </Col>
                        <Col md="2">  
                        <label>Prix Samedi</label>
                          <Input
                              className="form-control"
                              type="number"
                              min="0"
                              placeholder="Prix de samedi"
                              id="SAMEDI"
                          />
                        </Col>
                    </Row>
                    <Row className="mt-4">
                       
                       <Col md="2">  
                         <label>Prix dimanche</label>
                         <Input
                             className="form-control"
                             type="number"
                             min="0"
                             placeholder="Prix de dimanche"
                             id="DIMANCHE"
                         />
                        </Col>
                      
                      </Row>
                    <Row className="mt-4">
                    <Col md="6">
                      <label>Image(s)</label>
                      <Input
                          className="form-control"
                          type="file"
                           
                          data-preview=".preview"
                          placeholder="Photo"
                          id="autreFile"
                      />
                      </Col>
                     </Row>

                    <Row className="mt-4">
                             <Col md="12">
                                    <Card>
                                       
                                        <CardBody>
                                            <img style={{width:"900px",heigth:"200px"}} className="card-img-top" src={this.state.files_preview} alt="Image"/>
                                        </CardBody>
                                        
                                       
                                    </Card>
                              </Col>
                      </Row>
                      
                    <Row className="justify-content-start mt-5">
                    <Col md="6">
                        <Button  color="secondary" block className="b-lg">
                          Annuler
                        </Button>
                      </Col>
                      <Col md="6">
                        <Button onClick={this.handleUpdateTerrain} id="initial" color="success" block className="b-lg" >
                          Modifier la salle
                        </Button>
                        {/* <Button id="chargement" color="info" className="b-lg" type="button" disabled>
                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                          Envoi...
                        </Button> */}
                      </Col>
                     
                    </Row>
                  </CardBody>
                </Card>
              </CCol>
            </CRow>
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default ModifierSalle

